  body .control-admin {
    position: fixed;
    bottom: 15%;
    right: 1%;
    background: transparent;
    border-radius: 75%;
    padding: 0.2em;
    z-index: 2; }
    body .control-admin .toggle-admin {
      background-color: #020C1A;
      border-radius: 75%;
      z-index: 2;
      padding: 1.5em;
      width: 5em;
      height: 5em;
      border: 1px solid #020C1A;
      display: flex;
      justify-content: center;
      align-items: center;
      /* box-shadow: 0 4px 4px #aaa; */ }
      body .control-admin .toggle-admin i {
        color: white; }
      body .control-admin .toggle-admin:focus {
        outline: none; }
    body .control-admin .menu-admin {
      margin: 0;
      list-style: none;
      visibility: hidden; }
      body .control-admin .menu-admin .option-admin {
        position: absolute;
        background-color: #020C1A;
        border-radius: 75%;
        padding: 0.6em;
        color: white;
        width: 4em;
        height: 4em;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.5s ease;
        bottom: 7%;
        right: 3%; }
        body .control-admin .menu-admin .option-admin i {
          color: white; }
    body .control-admin .active.menu-admin .option-admin {
      visibility: visible;
      bottom: 8%;
      right: 1%; }
      body .control-admin .active.menu-admin .option-admin:nth-of-type(1) {
        transform: translate(0em, -6em); }
      body .control-admin .active.menu-admin .option-admin:nth-of-type(2) {
        transform: translate(-4em, -5em); }
      body .control-admin .active.menu-admin .option-admin:nth-of-type(3) {
        transform: translate(-6em, -1.3em); }
      body .control-admin .active.menu-admin .option-admin:nth-of-type(4) {
        transform: translate(-5em, 2.8em); }
    @media (min-width: 576px) {
      body .control-admin {
        bottom: 8%; } }

